.main {
  @media all and (min-width: 768px) {
    background-color: #f5f5f5;
    .wrapper {
      margin: auto;
      justify-content: center;
      display: flex;
      flex-direction: row-reverse;
      padding: 20px 40px 20px 0px;
    }
  }
}

.form {
  background-color: linear-gradient(#f5f5f5, #ffffff);
  margin-top: 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
